<template>
  <div class="pa-4">
    <h1>Promise</h1>
    <!-- https://wangdoc.com/javascript/async/promise -->
    <textarea
      v-model="msg"
      rows="10"
      readonly
      placeholder="输出结果"
      class="rounded pa-2"
      style="width: 100%;border: 1px solid lightblue;"
    />
    <div>
      <v-btn
        class="mr-2"
        @click="reset(),handleClick()"
      >
        按钮
      </v-btn>
      <v-btn
        class="mr-2"
        @click="reset(),handleClickP()"
      >
        Promise按钮
      </v-btn>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: '',
    }
  },

  methods: {
    asyncFn(arg, callback) {
      this.msg += '执行中... 参数:' + arg + '\n';
      console.log('执行中... 参数:', arg);
      setTimeout(() => {
        if (typeof callback === 'function') {
          callback(arg * 2);
        }
        return arg * 2
      }, 500)
    },

    // 解构打印
    print(...args) {
      console.log(...args);
      this.msg += args.join(' ')+ '\n';
    },

    reset() {
      this.msg = '';
    },

    handleClick() {
      this.asyncFn(2, () => {
        this.asyncFn(4, () => {
          this.asyncFn(23, () => {
            this.asyncFn(12, () => {
              this.asyncFn(34, () => {
                this.asyncFn(64, (arg) => {
                  this.print(arg);
                });
              });
            });
          });
        });
      });
    },

    handleClickP() {
      const that = this;
      const res = (new Promise((resolve, reject) => {resolve(that.asyncFn(2)), reject('err')}))
        .then((res) => console.log('成功', res), console.error(res))
      console.log(res);
    }
  }
}
</script>

